













































.meta {
  border-bottom: 1px solid #eee;
  padding: 5px 0;
}

.meta:after {
  content: '';
  display: block;
  clear: both;
}

.meta-label {
  display: inline-block;
  padding: 5px 10px;
  margin-left: 5px;
  background-color: #bbb;
  border-radius: 2px;
  font-size: 12px;
}



