

























.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 44px;
  box-shadow: 1px 1px 1px #ccc;
  background-color: #fff;
}

.title {
  font-family: Helvetica, sans-serif;
  line-height: 44px;
  margin: 0;
  padding: 0 10px;
  float: left;
}

.title .fa {
  font-size: 15px;
  color: hotpink;
  display: inline-block;
  padding: 0 1px;
}

.nav {
  float: right;
  margin: 0;
  padding: 0;
}

.nav li {
  float: left;
  list-style: none;
  line-height: 44px;
  padding: 0 8px;
}

.nav li:last-child {
  padding-right: 10px;
}

.nav a,
.nav a:visited,
.nav a:link {
  color: #333;
}

.nav a.active {
  color: dodgerblue;
}

.nav a:hover {
  color: #000;
}



