
























body {
  font-family: Arial, sans-serif;
  background-color: #fafafa;
}

a, a:link, a:visited {
  color: #000;
  text-decoration: none;
}

.container {
  width: 100%;
  max-width: 750px;
  margin: 0 auto;
}

body > .container {
  box-shadow: 0 1px 1px #ccc;
}



