



















.footer {
  margin-top: 30px;
  padding: 30px 0;
  font-size: 12px;
  clear: both;
  /*border-top: 1px solid #eee;*/
  text-align: center;
}

p.quote {
  margin: 0;
  padding: 0 10px;
  font-size: 13px;
  color: #999;
  font-style: italic;
  position: relative;
}

.quote .fa {
  position: relative;
  font-size: 8px;
}

.quote .fa-quote-left {
  top: -2px;
}

.quote .fa-quote-right {
  bottom: -2px;
}



